  transition: border-bottom .25s, box-shadow .25s; }
  h1 a.highlighter:hover, h2 a.highlighter:hover, h3 a.highlighter:hover, h4 a.highlighter:hover, h5 a.highlighter:hover, h5 a.highlighter:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -4px 0 rgba(56, 116, 168, 0.8) inset; }

p a.highlighter {
